.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.Avatar {
  border-radius: 50%;
}

.Avatar-Container {
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
}

.App-header {
  width: 100%;
  background-color: #282c34;
  height: 30vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Xoong {
  display: flex;
  justify-content: flex-start;
  /* align-items: center; */
  margin-top: 10vh;
  flex-direction: column;
}

.BapSong {
  width: 100%;
}

.BapChin {
  width: 100%;
  margin-top: 10vh;
  padding-top: 20;
  padding-bottom: 20;
}

.Button {
  width: 100%;
  height: 100%;
}

.BapChinContainer {
  margin-top: 5ch;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
